:root{--va-separator-height: 1px;--va-separator-background-color: var(--va-divider);--va-separator-margin-top: 1rem;--va-separator-margin-bottom: 1rem}.va-separator{height:var(--va-separator-height);background-color:var(--va-separator-background-color);margin-top:var(--va-separator-margin-top);margin-bottom:var(--va-separator-margin-bottom)}.va-tabs+.va-separator{margin-top:0}.va-card :is(.va-card__title,.va-card__content,.va-card__actions,.va-card_actions__vertical)>.va-separator{margin-right:calc(-1 * var(--va-card-padding));margin-left:calc(-1 * var(--va-card-padding))}
